// home.page.ts
import { Component, ViewChild, ElementRef, ComponentFactoryResolver } from '@angular/core';

import { Geolocation } from '@ionic-native/geolocation/ngx';
import { NativeGeocoder, NativeGeocoderResult, NativeGeocoderOptions } from '@ionic-native/native-geocoder/ngx';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, BehaviorSubject } from 'rxjs';
declare var google;

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {

  @ViewChild('map', { static: false }) mapElement: ElementRef;
  map: any;
  address: string;
  service: any;
  latitude: number;
  longitude: number;

  constructor(
    private geolocation: Geolocation,
    private nativeGeocoder: NativeGeocoder,
    private HttpClient: HttpClient) {
  }


  ngOnInit() {
    this.loadMap();
  }

  loadMap() {
    this.geolocation.getCurrentPosition().then((resp) => {

      this.latitude = resp.coords.latitude;
      this.longitude = resp.coords.longitude;

      let latLng = new google.maps.LatLng(resp.coords.latitude, resp.coords.longitude);
      let Guatemala = { lat: 15.7835, lng: -90.2308 };
      let mapOptions = {
        center: Guatemala,
        zoom: 9,
        mapTypeId: google.maps.MapTypeId.ROADMAP
      }   

      this.map = new google.maps.Map(this.mapElement.nativeElement, mapOptions);    

      var request = {
        location: Guatemala,
        radius: 50000,
        type: ['city_hall']
      };
      this.service = new google.maps.places.PlacesService(this.map);
      this.service.nearbySearch(request, (results, status) => {
        if (status === google.maps.places.PlacesServiceStatus.OK) {
          console.log('results.length', results.length);
          for (let i = 0; i < results.length; i++) {
            console.log('results', results[i]);

            this.getWether(results[i].geometry.location.lat(), results[i].geometry.location.lng()).subscribe(res => {
              console.log('let datainfo =', res);
              let data = JSON.stringify(res);
              let content =
                `<h4>Current Weather!</h4>
               <div>${data}</div>                
              `;
              this.addMarker(this.map, res.coord.lat, res.coord.lon, content);

            });

          }

        }
      });





    }).catch((error) => {
      console.log('Error getting location', error);
    });
  }
  getWether(lat: any, lon: any): Observable<any> {
    const headers = new HttpHeaders()
      .set('x-rapidapi-host', 'community-open-weather-map.p.rapidapi.com')
      .set('x-rapidapi-key', '0ff4de13e9mshbafda0022c0c6cfp18355ajsn2554e0eb87f4');
    return this.HttpClient.get(`https://community-open-weather-map.p.rapidapi.com/weather?lat=${lat}&lon=${lon}`, { headers: headers, });
  }

  addMarker(map: any, lat: any, lng: any, content: any) {

    let marker = new google.maps.Marker({
      map: map,
      animation: google.maps.Animation.DROP,
      position: { lat, lng },
    });
    this.addInfoWindow(marker, content);
  }
  addInfoWindow(marker, content) {
    let infoWindow = new google.maps.InfoWindow({
      content: content
    });
    google.maps.event.addListener(marker, 'click', () => {
      infoWindow.open(this.map, marker);
    });
  }

}
